import axios from 'axios'
import React, { useState,useEffect ,useContext} from 'react'
import M from 'materialize-css';
import {BgThemeContext} from '../context/bgtheme';

function AdminDepartment() {

    const[deptinfo,setdeptinfo]=useState([]);

    var res;
    const {appcolor} = useContext(BgThemeContext);
    var color= appcolor;

    const getDeptInfo = () =>{
        console.log("admin dept res")
        var card_array=[]
        res =  axios.get('https://workflow-app-01.herokuapp.com/admin/deptartment/get')
        .then(function(response){
            console.log(response.data)
            var deptinformation= response.data;
            
            for(let i=0;i<deptinformation.length;i++)
            {
                let obj = deptinformation[i];
                let card = 
                <div className="card">
                    <div class="card-content">
                        <div class="row">
                            <div class="col l2">000{obj.id}</div>
                            <div class="col l2">{obj.name}</div>
                            <div class="col l2">{obj.function_name}</div>
                            <div class="col l2">{obj.location}</div>
                            <div class="col l2">{obj.employee_count}</div>
                            <div class="col l2">
                                <span class="badge">
                                    <a data-target="dde" class='dropdown-trigger'  ><i class=" material-icons" style={{color:color,width:"30px" ,height:"30px"}}>more_vert</i></a>
                                    <a data-target="dde" class='dropdown-trigger'  ><i class=" material-icons" style={{color:color,width:"30px" ,height:"30px"}}>more_vert</i></a>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>

                card_array.push(card);

            }

           
        })
        .catch(function(err)
        {
            console.log(err);
        })
        return card_array;
    }



    useEffect(()=>{
        M.AutoInit(); 
        
    },[])

    return (
        <>
        <div class="container-info" style={{width:"76rem"}}>
            <h4>Departments</h4>
            <div class="row" class="row admin-table-header">
                <div class="col l2">CODE</div>
                <div class="col l2">DEPARTMENT NAME</div>
                <div class="col l2">FUNCTION</div>
                <div class="col l2">LOCATION</div>
                <div class="col l2">EMPLOYEE COUNT</div>
                <div class="col l2"></div>
            </div>
        </div>

        {getDeptInfo()}
        </>
    )
}

export default AdminDepartment
